<template>
  <div>
    <b-container>
      <b-col class="m-5 text-center">
        <h2>These are my blogs</h2>
      </b-col>
      <b-row class="justify-content-end m-3">
        <b-col md="4" class="d-flex align-items-center justify-content-end">
          <b-form-input v-model="search" placeholder="請輸入關鍵字以搜尋文章" />
          &nbsp;<b-button @click="showModal = true" class="ml-2 filter-btn">{{ $t('filter') }}</b-button>
        </b-col>
      </b-row>
      <b-row align-h="center" class="m-3">
        <table class="table table-striped table-hover">
          <thead>
            <tr>
              <th scope="col">{{ $t('Title') }}</th>
              <th scope="col">{{ $t('Subject') }}</th>
              <th scope="col">{{ $t('Author') }}</th>
              <th scrpe="col">{{ $t('Summary') }}</th>
              <th scrpe="col">{{ $t('Link') }}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="blog in filteredBlogs" :key="blog.id">
              <td>{{ blog.title }}</td>
              <td>{{ blog.subject }}</td>
              <td>{{ blog.author }}</td>
              <td>{{ blog.summary }}</td>
              <td><button @click="redir(blog.id)">{{ $t('SeeAll') }}</button></td>
            </tr>
          </tbody>
        </table>
      </b-row>
      <b-modal v-model="showModal" :title="$t('filter')" @ok="applyFilter">
        <p>請注意，本功能尚未完善，一次只能勾選一個。如果使用中出現問題請重新整理網頁</p>
        <p>Select subject(s) to filter:</p>
        <b-form-checkbox v-model="mathChecked" name="數學">Math</b-form-checkbox>
        <b-form-checkbox v-model="chineseChecked" name="國文">Chinese</b-form-checkbox>
        <b-form-checkbox v-model="biologyChecked" name="生物">Biology</b-form-checkbox>
      </b-modal>
    </b-container>
  </div>
</template>

<script>
import { db } from "@/firebaseConf.js";
import { collection, getDocs } from "firebase/firestore";

export default {
  data() {
    return {
      headers: [],
      blogs: [],
      search: "",
      showModal: false,
      mathChecked: false,
      chineseChecked: false,
      biologyChecked: false,
    };
  },
  mounted() {
    this.getBlogs();
  },
  methods: {
    redir(id) {
      this.$router.push("/show/" + id);
    },
    async getBlogs() {
      const querySnapshot = await getDocs(collection(db, "blogs"));
      const blogs = [];
      querySnapshot.forEach((doc) => {
        console.log(`${doc.id} => ${doc.data()}`);
        blogs.push({
          id: doc.id,
          timestamp: doc.data().timestamp,
          title: doc.data().title,
          subject: doc.data().subject,
          author: doc.data().author,
          summary: doc.data().summary,
          hover: false,
        });
      });
      blogs.sort((b,a) => b.timestamp - a.timestamp);
      this.blogs = blogs;
    },
    filterBlogs() {
      let filteredRows = this.blogs.filter((blog) => {
        const isSearchIncluded =
          blog.title.toLowerCase().includes(this.search.toLowerCase()) ||
          blog.summary.toLowerCase().includes(this.search.toLowerCase());
        return isSearchIncluded;
      });
      if (this.mathChecked) {
        filteredRows = filteredRows.filter((blog) =>
          blog.subject.includes("數學")
        );
      }
      if (this.chineseChecked) {
        filteredRows = filteredRows.filter((blog) =>
          blog.subject.includes("國文")
        );
      }
      if (this.biologyChecked) {
        filteredRows = filteredRows.filter((blog) =>
          blog.subject.includes("生物")
        );
      }
      return filteredRows;
    },
    resetFilters() {
      this.search = "";
      this.mathChecked = false;
      this.chineseChecked = false;
      this.biologyChecked = false;
    },
  },
  computed: {
    filteredBlogs() {
      return this.filterBlogs();
    },
  },
};
</script>

<style scoped>
.filter-btn {
  width: 100px;
}

.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
  max-width: 1500px;
  margin: 0 auto;
}

</style>
