<template>
  <div class="container">
    <h3>{{ $t('Login') }}</h3> <br>
    <form @submit.prevent="login">
      <div class="form-group"  >
        <h5>{{ $t('USRname') }}</h5> 
        <b-form-input
          class="form-control form-control-lg"
          v-model="usr"
          type="text"
          :placeholder="$t('ETRAccount')"
          required
        />
        <br>
        <h5>{{ $t('Password') }}</h5>
        <b-form-input
          class="form-control form-control-lg"
          v-model="psw"
          type="password"
          :placeholder="$t('ETRPSW')"
          required
        />
      </div>
      <br>
      <button type="submit" class="btn btn-primary">{{ btnText }}</button>
    </form>

  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      rows: [],
      usr: '',
      psw: '',
      showPassword: false,
      btnText: 'Submit',
    };
  },
  methods: {
    login(){
      this.btnText='處理中，請稍後';
      // 從 Google Sheets 取得資料，並找出指定時間的文章
      axios.get('https://script.google.com/macros/s/AKfycbyGb1Ti5VO37f3z7bNjmmf9FMoa43zlRJXFnyemP_s0T-qSyrFzmwmkmCXgdmkoduZb/exec')
        .then(response => {
          this.rows = response.data;
          const usrname = this.rows.find(row => row.username == this.usr);
          if(!usrname){
            alert("帳號錯誤");
            location.reload();
          }else if(usrname.password != this.psw){
            alert("密碼錯誤");
            location.reload();
          }else {
            alert("登入成功！");
            this.$store.dispatch('setLogin', true);
            this.$store.dispatch('setName',usrname.realName);
            this.$store.dispatch('setStatus',usrname.status);
            this.$router.push('/');
          }
        })
        .catch(error => {
          alert(error);
        });      
    }
  }
};
</script>

