.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 5px;
  font-family: Arial, Helvetica, sans-serif;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}
</style>
