<template>
  <div class="container">
    <form @submit.prevent="submitForm">
      <label for="title">{{ $t('Title') }}</label>
      <input v-model="form.title" type="text" placeholder="請輸入標題" required>

      <label for="author">{{ $t('Author') }}</label>
      <input v-model="form.author" type="text" placeholder="請輸入作者" disabled>

      <label for="summary">{{ $t('Summary') }}</label>
      <input v-model="form.summary" type="text" placeholder="摘要請勿超過50字" maxlength="50" required>
      
      <label for="content">內容(建議放大後編輯)</label>
      <v-md-editor v-model="form.content" style="height: 500px;"/>

      <br>
      <label for="subject">{{ $t('Subject') }}</label>
      <select v-model="form.subject">
        <option v-for="(option, index) in subjectlist" :key="index" :value="option">{{ option }}</option>
      </select>
      <label for="videos">{{ $t('Video') }}</label>
      <input v-model="form.video" type="text" placeholder="請貼上youtube分享的嵌入碼">
      
      <label for="hashtags">HashTags</label>
      <input v-model="form.tags" type="text" placeholder='請輸入標籤，請以","間隔'/>
      <input type="submit" value="Submit" name="submit">
      <p v-if="showText">處理中，請稍候</p>
      <br> <br>
    </form>
  </div>
</template>

<script>
import { db } from "@/firebaseConf.js";
import { collection, addDoc } from "firebase/firestore"; 
import firebase from 'firebase/compat/app';
import 'firebase/compat/firestore';


export default {
  data() {
    return {
      form: {
        title: '',
        author: this.$store.getters.getName,
        summary: '',
        content: '',
        video: '',
        subject: '',
        tags: '',
      },
      subjectlist: ['國文','數學','英文','物理','化學','生物','地科','資訊','社會','其他'],
      showText: 0,
    }
  },

  mounted(){
    if(this.$store.getters.getLogin==false){
      alert("請先登入");
      this.$router.push("/login");
    }
  },
  methods: {
    submitForm(){
      this.showText=1;
      const docRef =  addDoc(collection(db,"blogs"), {
        timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        title: this.form.title,
        author: this.form.author,
        summary: this.form.summary,
        content: this.form.content,
        subject: this.form.subject,
        tags: this.form.tags,
        video: this.form.video,
        authentication: "authenticated",
			});
			alert("成功新增", docRef.id);
      this.$router.push("/blogs");
    }
  }
}
</script>

<style scoped>
* {box-sizing: border-box;}

input[type=text], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=submit] {
  background-color: #04AA6D;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #45a049;
}

.v-md-editor{
  height: 300px;
}


.container {
	border-radius: 5px;
	background-color: #f2f2f2;
	padding: 20px;
	max-width: 1500px;
	margin: 0 auto;
	font-family: Arial, Helvetica, sans-serif;  
}

</style>
