<template>
  <div class="container">
		<div v-if="blog">
			<h2>Edit Page</h2>

			<b-icon icon="pencil-square"/>
			<form @submit.prevent="submitForm">
      <label for="title">{{ $t('Title') }}</label>
      <input v-model="blog.title" type="text" placeholder="請輸入標題" required>

      <label for="author">{{ $t('Author') }}</label>
      <input v-model="blog.author" type="text" placeholder="請輸入作者" disabled>

      <label for="summary">{{ $t('Summary') }}</label>
      <input v-model="blog.summary" type="text" placeholder="摘要請勿超過50字" maxlength="50" required>
      
      <label for="content">內容(建議放大後編輯)</label>
      <v-md-editor v-model="blog.content" style="height: 500px;"/>

      <br>
      <label for="subject">{{ $t('Subject') }}</label>
      <select v-model="blog.subject">
        <option v-for="(option, index) in subjectlist" :key="index" :value="option">{{ option }}</option>
      </select>
      <label for="videos">{{ $t('Video') }}</label>
      <input v-model="blog.video" type="text" placeholder="請貼上youtube分享的嵌入碼">
      
      <label for="hashtags">HashTags</label>
      <input v-model="blog.tags" type="text" placeholder='請輸入標籤，請以","間隔'/>
      <input type="submit" value="Submit" name="submit">
      <p v-if="showText">處理中，請稍候</p>			
    </form>		

		
		</div>
		<div v-else>blog not found</div>
</div>
</template>

<script>
import { db } from "@/firebaseConf.js";
import { doc, getDoc, updateDoc } from "firebase/firestore";

export default {
  data() {
    return {
      blog: null,
			subjectlist: ['國文','數學','英文','物理','化學','生物','地科','資訊','社會','其他'],
			blogId: this.$route.params.id,
			authorized: false,
    };
  },
  mounted() {
		this.getBlog(this.blogId);
		if(this.$store.getters.getLogin==false){
			alert("請先登入");
			this.$router.push("/login");
		}
	},
  methods: {
    async getBlog() {
      const blogRef = doc(db, "blogs", this.blogId);
      const docSnapshot = await getDoc(blogRef);
      if (docSnapshot.exists()) {
        this.blog = docSnapshot.data();
      } else {
        alert("Blog not found!");
      }
    },
		async submitForm(){
		//	this.authorized=false;
			if(this.$store.getters.getName===this.blog.author){
				this.authorized=true;
			}
			if(this.$store.getters.getStatus<2){
				this.authorized=true;	
			}
			if(this.authorized==false){
				alert("權限不足(僅超級管理員以及原作者可以編輯此篇文章)！");
				this.$router.push("/show/"+this.blogId);				
			}else{
				const docRef = doc(db, "blogs", this.blogId);
				try {
					await updateDoc(docRef, this.blog);
					this.$router.push("/blogs");
					alert("文件更新成功！");
				} catch (error) {
					alert("文件更新失敗：", error);
					this.$router.push("/blogs");
				}
			}
		}
  },
};
</script>

<style scoped>
