<template>
  <div class="container">
    <h2>註冊</h2>
		(*必填)
    <form @submit.prevent="registerUser">
      <div class="form-group">
        <label for="RealName">真實姓名*</label>
        <input type="text" id="real_name" v-model="RealName" required>

				<label for="NickName">暱稱*</label>
				<input type="text" id="nick_name" v-model="NickName" required>
				
				<label for="Gender">性別*</label>
				<select name="Gender" id="gender" v-model="Gender" required>
					<option value="male">男</option>
					<option value="female">女</option>
				</select>

				<label for="Birthday">出生日期*</label> <br>
				<input type="date" name="birthday" id="bd" v-model="BirthDay" required> <br> <br>

				<label for="Phone">電話號碼</label>
				<input type="text" name="phone" id="phone_number" v-model="Phone">

				<label for="Mood">留言板</label>
				<textarea name="mood" id="mood" v-model="Mood"/>

        <input type="submit" value="送出">
      </div>
    </form>
  </div>
</template>

<script>
import 'firebase/auth';
//import { auth } from '@/firebaseConf';
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { db } from "@/firebaseConf.js";
import { doc, setDoc, serverTimestamp } from "firebase/firestore";

export default {
  data() {
    return {
			user_local: '',
      RealName: '',
			NickName: '',
			Gender: '',
			BirthDay: '',
			Phone: '',
			Mood: '',
    }
  },
	mounted(){
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
				this.user_local=user;
      } else {
				alert("please login first!");
				this.$router.push("/lr");
      }
    });
	},
  methods: {
		async registerUser(){
			const data={
        lastUpdate: serverTimestamp(),
        name: this.RealName,
				nickname: this.NickName,
				gender: this.Gender,
				birthday: this.BirthDay,
				phone: this.Phone,
				mood: this.Mood,
				status: 2,
      }
      setDoc(doc(db, "usrs", this.user_local.uid), data);
			alert("更新成功！");
			this.$router.push("/admin");
		}
  }
}
</script>

