<template>
  <div>
    <div class="container" v-if="blog">
      <div class="title-wrapper">
        <h2>{{ blog.title }}</h2>  
        <b-button v-if="this.$store.getters.getLogin==true" variant="link" @click="editPage">[Edit]</b-button>
        <b-button variant="link" @click="this.$router.push('/112APCSBasic')">[回上頁]</b-button>
      </div> 
      <h6>{{ $t('Author') }}: {{ blog.author }}</h6>
      <h6>{{ $t('Subject') }}: {{ blog.subject }}</h6>
      <h6>{{ $t('PostTime') }}：{{ formatDate(blog.timestamp) }}</h6>
      <div v-if="blog.video" class="video-container" v-html="blog.video"/>
      <v-md-editor v-model="blog.content" mode="preview" @copy-code-success="handleCopyCodeSuccess"/>
    </div>
    <div v-else>
      <p>Loading...</p>
    </div>
  </div>
</template>

<script>
import { db } from "@/firebaseConf.js";
import { doc, getDoc } from "firebase/firestore";
import { format } from 'date-fns';


export default {
  data() {
    return {
      blog: null,
    };
  },  
  mounted() {
    const blogId = this.$route.params.id;
    this.getBlog(blogId);
  },
  computed:{
    getLink(){
      return this.$route.path + "/edit";
    }
  },
  methods: {
    async getBlog(blogId) {
      const blogRef = doc(db, "APCSBasic", blogId);
      const docSnapshot = await getDoc(blogRef);
      if (docSnapshot.exists()) {
        this.blog = docSnapshot.data();
      } else {
        alert("Blog not found!");
        this.$router.push("/blogs");
      }
    },
    formatDate(timestamp) {
      const date = timestamp.toDate();
      return format(date, 'yyyy-MM-dd HH:mm:ss');
    },
    editPage(){
      this.$router.push("/showAPCS/"+this.$route.params.id+"/edit");
    }
  },
};
</script>

