## 關於

努力了好久，終於成功了。

在高二時我一直有個想法，就這樣高中畢業過個幾屆自己就會像是那些回來演講的大學長，後代的學弟幾乎都不認識。因此我想要給宜中留下些什麼。適逢當時學習化學時遇到瓶頸，上網查到的資料參差，因此我想建立一個「宜中自學平台」讓後面的學弟妹少走一些彎路。

一所學校的底蘊在哪？宜中跟建北等名校差在哪？答案是學生。或許名校的老師有較為特殊的風格或是較為豐富的經驗，但同考到教師證，老師們的品質絕不會和其他高中差太多。再者，老師再強若學生能力不足，能傳授的知識亦有限。若想改變一所學校，何不從學生開始著手？

當年我是這麼聽國中老師（50多歲的宜中大學長）評價宜蘭的兩所學校的：羅高的普通班資質比宜中更強，但宜中的資優班又領先羅高一大截。白話來說，兩所學校的平均其實是差不多，但是宜中內部的實力差距比較大。這也是我選擇離家較遠的宜中的原因，我想要得到更多的資源。

既然如此，何不讓宜中最頂尖的學生拉普通班一把呢？

為節省通勤時間，從高二起我開始住校。高二時的室友是體育班的學弟，課內成績對他們而言固然重要，但也沒有到不可或缺的地步。高三上的室友是幾個普通班的學生，成績皆在年級的中後段，是連前段班都進不了。其中一個數學模擬考僅有3級分，大概我寫完單選成績都有機會比他高。

從我住進去開始，我就帶著他們一起努力，他們的成績也變得更好，到了學測，原本數學僅有3級分的同學進步到了9級。僅我一人便能有這種效果，假使宜中所有前端班一起努力，能否令宜中有朝一日，成為全台公認的「東北一中」呢？

於是，這個網站就誕生了。

網站中有許多新創的功能，比如未來會出現的「實物圖書館」，旨在共享大家手中有的資源。比如說考完TOEIC但是題本沒寫完，就可以將題本贈送給有需要的人。尚未動工的「卓越會」是宜中資訊最高殿堂，任何跟資訊有關的都會在裡面，包括了筆者的網頁技能等等。還有配套的「YLSH Online Judge」等等，都是未來會出現的。

本站使用的前端框架是Vue.js 3.0，後端為Google App Script，資料庫為Google Sheet，都是免費的東西，但是處理速度有些緩慢（需要讀取資料庫的頁面大概要3~4秒），如果未來能有資金的話我可以把資料庫替掉，增加使用體驗，但我認為現階段先這樣就夠了。

希冀未來某天，當台灣人提到東北一中，會第一個聯想到宜蘭高中而非某個很醜的T-shirt（蘿糕真是有夠白痴，自封東北一中還把字樣印在T-shirt上賣）

*20230504 戴偉璿 筆*
