<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="info">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
      <b-navbar-brand class="mr-5">
        <img
          src="../assets/logo.png"
          width="30"
          class="d-inline-block align-top"
          alt="vue_icon"
        />
        宜蘭高中自學平台
      </b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav>
          <b-nav-item><router-link to="/" exact>{{ $t('Home') }}</router-link></b-nav-item>
          <b-nav-item><router-link to="/blogs" exact>{{ $t('Posts') }}</router-link></b-nav-item>
          <b-nav-item><router-link to="/about" exact>{{ $t('About') }}</router-link></b-nav-item>
          <b-nav-item><router-link to="/excellent" exact>{{ $t('ExcellentClub') }}</router-link></b-nav-item>
          <b-nav-item><router-link to="/realLib">實物圖書館</router-link></b-nav-item>
          <b-nav-item><router-link to="/test"  exact>Test</router-link></b-nav-item>
          <b-nav-item-dropdown :text="$t('Lang')">
            <b-dropdown-item @click="this.$i18n.locale='zh'">中文</b-dropdown-item>
            <b-dropdown-item @click="this.$i18n.locale='en'">English</b-dropdown-item>
          </b-nav-item-dropdown>

        </b-navbar-nav>
        <b-navbar-nav class="ml-auto">
          
          <b-nav-item v-if="this.$store.getters.getLogin==false"><router-link to="/login" style="color: black;">{{ $t('Login') }}</router-link></b-nav-item>
          <b-nav-item v-if="this.$store.getters.getLogin==false"><router-link to="/register" style="color: black;">{{ $t('Register') }}</router-link></b-nav-item>
          <!-- 
          <b-nav-item v-if="this.$store.getters.getLogin==false"><router-link to="/auth" style="color: black;">登入/註冊</router-link></b-nav-item>
-->
          <b-nav-item-dropdown :text="getName"  right v-if="this.$store.getters.getLogin==true">
            <b-dropdown-item href="#">{{ $t('Account') }}</b-dropdown-item>
            <b-dropdown-item href="#">{{ $t('Setting') }}</b-dropdown-item>
            <b-dropdown-item><router-link to="/addPage" style="color: black;">{{ $t('AddBlog') }}</router-link></b-dropdown-item>
            <b-dropdown-item @click="logout">{{ $t('Logout') }}</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
import 'firebase/compat/auth';
import { auth } from '@/firebaseConf';
import { getAuth, onAuthStateChanged } from "firebase/auth";

export default {
  methods: {
    toggleNavbar() {
      const navbar = document.querySelector('.navbar');
      navbar.classList.toggle('open');
    },
    logout(){
      this.$store.dispatch('setLogin', false);
      auth.signOut();
      location.reload();
    }
  },
  computed: {
    getName(){
      return this.$store.getters.getName;
    }
  },
  mounted() {
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        // User is signed in, see docs for a list of available properties
        // https://firebase.google.com/docs/reference/js/auth.user
        this.$store.dispatch("setLogin",true);
        this.$store.dispatch("setUid", user.uid);
        // ...
      }else{
        this.$store.dispatch("setLogin",false);
      }
    });
  },
};
</script>

