<template>
  <div>
    <b-container>
      <div class="title-wrapper">
        <h2>112學年度蘭燈之星APCS基礎班</h2>
        <b-button variant="link" @click="this.$router.push('/excellent')">[回上頁]</b-button>
      </div> 

      <div v-for="blog in blogs" :key="blog.id">
        <b-card
          class="card"
          :title="blog.title"
          @click="this.$router.push('showAPCS/'+blog.id)"
        >
          <b-card-text>
            {{ blog.summary }}
          </b-card-text>
        </b-card>        
      </div>

    </b-container>
  </div>
</template>

<script>
import { db } from "@/firebaseConf.js";
import { collection, getDocs } from "firebase/firestore";

export default {
  data() {
    return {
      blogs: [],
    };
  },
  mounted() {
    this.getBlogs();
  },
  methods: {
    async getBlogs() {
      const querySnapshot = await getDocs(collection(db, "APCSBasic"));
      const blogs = [];
      querySnapshot.forEach((doc) => {
        console.log(`${doc.id} => ${doc.data()}`);
        blogs.push({
          id: doc.id,
          timestamp: doc.data().timestamp,
          title: doc.data().title,
          author: doc.data().author,
          summary: doc.data().summary,
          hover: false,
        });
      });
      blogs.sort((b,a) => b.timestamp - a.timestamp);
      this.blogs = blogs;
    },
  },
};
</script>

