<template>
  <div class="container">
    <h2>登入狀態</h2>
    <div v-if="user">
      <p>當前用戶：{{ this.user.name }}</p>
      <p>電子郵件：{{ user.email }}</p>
      <p>電話號碼：{{ user.phoneNumber }}</p>
      <button @click="logout">登出</button>
    </div>
    <div v-else>
      <p>未登入</p>
      <button @click="login">登入</button>
    </div>
  </div>
</template>

<script>
import 'firebase/compat/auth';
import firebase from 'firebase/compat/app';

import { auth } from '@/firebaseConf';
import { getAuth, onAuthStateChanged } from "firebase/auth";
//import { addDoc } from "firebase/firestore"; 


import { db } from "@/firebaseConf.js";
import { collection, getDocs, getDoc, doc, setDoc } from "firebase/firestore";

export default {
  data() {
    return {
      user: null,
      expirationTime: null, // 新增剩餘時間的資料屬性
      usrs: [],
    };
  },
  async mounted() {
    await this.getUsers();
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        this.$store.dispatch("setLlogin", true);
        this.user = user;

        const finduser = this.usrs.some((usr) => usr.id === user.uid);
        console.log(finduser);
        if (finduser) {
          console.log("user exist!")
          this.getLoginUser(user.uid).then(() => {
            console.log("name", this.user.name);
          });
          console.log("name",this.user.name);
        } else {
          console.log("user not exist,creat new user");
          // const docRef = addDoc(collection(db, "usrs"), {
          //   timestamp: firebase.firestore.FieldValue.serverTimestamp(),
          //   uid: this.user.uid,
          // });
          const data={
            registertime: firebase.firestore.FieldValue.serverTimestamp(),
            email: user.email,
            status: 4,
          }
          setDoc(doc(db, "usrs", this.user.uid), data);
          
          console.log("Add user");
        }
      } else {
        console.log("user not login");
      }
    });

  },
  methods: {
    login() {
      alert(this.user.uid);
    },
    logout() {
      auth.signOut();
    },
    async getUsers() {
      const querySnapshot = await getDocs(collection(db, "usrs"));
      const usrs = [];
      querySnapshot.forEach((doc) => {
        console.log(`${doc.id} => ${doc.data()}`);
        usrs.push({
          id: doc.id,
          uid: doc.data().uid,
        });
      });
      this.usrs = usrs;
    },
    async getLoginUser(uid){
      if(uid){
        const docSnapshot = await getDoc(doc(db, "usrs", uid));
        this.user=docSnapshot.data();
      }
    },
  }
};
</script>

